import { h, Component } from 'preact';
import style from './style';


export default class Details extends Component {
	
	render(){
		return  <div class={style.center}>
                        <div class={style.title}>
                            <div class={style.titletext}>
                                your details
                            </div>
                        </div>
                        <div class={style.centercontent}>
                        <div class={style.centerleft}>
                            <div class={style.detailtitle}>
                                <div class={style.detailtext}>your name and address</div>
                            </div>
                            <div class={style.inputcell}>
                                <div class={style.label}> your name </div>
                                <div class={style.inputline}> <input class={style.input} type="text"></input> </div>
                            </div>
                            <div class={style.inputcell}>
                                <div class={style.label}> address line one </div>
                                <div class={style.inputline}> <input class={style.inputlong} type="text"></input> </div>
                            </div>
                            <div class={style.inputcell}>
                                <div class={style.label}> address line two </div>
                                <div class={style.inputline}> <input class={style.inputlong} xtype="text"></input> </div>
                            </div>
                            <div class={style.inputcell}>
                                <div class={style.label}> postcode </div>
                                <div class={style.inputline}> <input class={style.inputshort} type="text"></input> </div>
                            </div>
						</div>
                        <div class={style.centerright}>
                            <div class={style.detailtitle}>
                                <div class={style.detailtext}>your contact details</div>
                            </div>
                            <div class={style.inputcell}>
                                <div class={style.label}> your email </div>
                                <div class={style.inputline}> <input class={style.inputlong} type="text"></input> </div>
                            </div>
                            <div class={style.inputcell}>
                                <div class={style.label}> your telephone </div>
                                <div class={style.inputline}> <input class={style.input} type="text"></input> </div>
                            </div>
						</div>
                        
                        </div>
						
			    </div>
	}

}



